


<style lang='scss'>
@import url('./index.scss');
@import url('../commom.scss');
@import url('Animate');

$themeColor: #fff;
$themeBgc: green;

body {
  font: Helvetica, sans-serif;
  color: $themeColor;
  background-color: $themeBgc;
  .logo {
    width: 400px;
    height: 400px;
    background-color: green;
  }
  .show{
    width: 200px;
    height: 200px;
    background-color: red;
  }
}

</style>

<template>
  <div>
    <h1 class="font-color">{{msg}}</h1>
    <div class="logo">
    <transition name="bounce" enter-active-class="animated bounceInLeft" leave-active-class="animated bounceOutRight">
        <div class="box"></div>
      </transition>
      
    </div>
    <div class="test"></div>
    <div class="show">我爱你</div>
    <Beader></Beader>
  </div>
</template>

<script>
import Beader from '../components/header/header'
export default {
  data () {
    return {
      msg: 'Hello from another module!'
    }
  },
  components: {
    Beader
  }
}
</script>


